

























import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({})
export default class InventoryActions extends Vue {
  @Prop({ default: false })
  selected!: boolean;

  actions = [
    { icon: "archive", emit: "archive" },
    { icon: "cloud_download", emit: "download" },
    { icon: "playlist_add", emit: "list-add" },
  ];
}
